.header {
  position: fixed;
  top: 0;
  height: var(--header-height);
  min-height: 70px;
  color: var(--primary-text-color);
  width: var(--app-width);
  padding: 15px 25px;
  border: var(--default-border);
  border-top: none;
  z-index: 99;
  background-color: var(--background-color);
}